import React,{useEffect,useState} from 'react';

const Grid4 = (props)=>{
    const[prop,setProp] = useState({backgrounds : [],images : [],title : []});
    useEffect(()=>{
        try{
            setProp(props.state);
        }catch(e){
            console.error(e)
        }
    },[prop])
    return(
    <div className="column is-half" style={{display:props.visible}}>
        <div className="columns is-multiline _query">
            <div className="column is-half _card">
                <p style={{background:prop.backgrounds[1],height:"calc(176px)",position:"relative",overflow:"hidden"}} class="middle">
                    <img src={prop.images[0]} alt={prop.title[0]} style={{position:"absolute"}}/>
                    <p className="title tag is-link" style={{zIndex:"2"}}>{prop.title[0]}</p>
                </p>
            </div>
            <div className="column is-half _card">
                <p style={{background:prop.backgrounds[2],height:"calc(176px)",position:"relative",overflow:"hidden"}} class="middle">
                    <img src={prop.images[1]} alt={prop.title[1]} style={{position:"absolute"}}/>
                    <p className="title tag is-success" style={{zIndex:"2"}}>{prop.title[1]}</p>
                </p>
            </div>
            <div className="column is-half _card">
                <p style={{background:prop.backgrounds[3],height:"calc(176px)",position:"relative",overflow:"hidden"}} class="middle">
                <img src={prop.images[2]} alt={prop.title[2]} style={{position:"absolute"}}/>
                    <p className="title tag is-danger" style={{zIndex:"2"}}>{prop.title[2]}</p>
                </p>
            </div>
            <div className="column is-half _card" style={{}}>
                <div style={{background:prop.backgrounds[4],height:"calc(176px)",position:"relative",overflow:"hidden"}} class="middle">
                    <img src={prop.images[3]} alt={prop.title[3]} style={{position:"absolute"}}/>
                    <p className="title tag is-primary" style={{zIndex:"2"}}>{prop.title[3]}</p>
                </div>
            </div>
        </div>
    </div>
    );
}
export default Grid4;