import React,{useRef} from 'react';
import Bulma from '@vizuaalog/bulmajs';
import {useNavFixed} from '../../hooks/useNavFixed';
import academia from '../../medias/images/academy (2).png';
import {BrowserRouter as Router,
        Switch,
        Route,
        Link} from 'react-router-dom';
const NavBar = ({background})=>{
  const background1 =background||"#ffffff";
  const message = useRef()
    useNavFixed(".fix")
    const handldeMessage = (e)=>{
      console.log("active")
     message.current.style.display="block"

    }
    const handldeClose = (e)=>{
      message.current.style.display="none"

    }
    return(
      <>
    
      <nav className="navbar fix" style={{width:"vw",color:"white",background:background1}}>
        <div className="navbar-brand">
          <Link className="navbar-item" to="/portafolio">
              <img src={academia}/>
              {'\u00A0'}
              MS Academy
          </Link>

          <div className="navbar-burger" data-target="navbarExampleTransparentExample">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>

        <div id="navbarExampleTransparentExample" className="navbar-menu" style={{background:background1}}>
          <div className="navbar-start">
            <a className="navbar-item" href="#" style={{position:"relative"}} onClick={handldeMessage}>
            Cursos
            </a>
            <Link to="/portafolio/cv" className="navbar-item">
                cv
            </Link>
            <div className="navbar-item has-dropdown is-hoverable">
              <a className="navbar-link" href="#">
                <p>Contacto</p>
              </a>
              <div className="navbar-dropdown is-boxed">
                <a className="navbar-item" href="https://github.com/Maynor2000" target="_blank" rel="noopener">
                <img src="https://img.icons8.com/windows/32/000000/github.png"/>{"\u00A0"}Mi github
                </a>
                <a className="navbar-item" href="https://www.facebook.com/mayk.akopii.7/" target="_blank" rel="noopener">
                <img src="https://img.icons8.com/nolan/64/facebook.png"/>{"\u00A0"}Facebook
                </a>
              </div>
            </div>
          </div>

          <div className="navbar-end">
            <div className="navbar-item">
              <div className="field is-grouped">
                <p className="control">
                </p>
                <p className="control" style={{width:"80%"}}>
                  <a style={{width:"100%"}} className="button button is-primary is-outlined" href="https://github.com/Maynor2000/portafolio/raw/0c8e0984731b247f87b04af06387bb366390e310/src/medias/images/Maynor%20Alvarez%20Acopa%20cv.pdf">
                    <span className="icon">
                      <i className="fas fa-download"></i>
                    </span>
                    <span>Descargar C.V</span>
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>

      </nav> 
      </>  
    )
}
export default NavBar;