import React,{useEffect} from 'react';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import CardsSection from './CardsSection';
import '@splidejs/splide/dist/css/themes/splide-skyblue.min.css';
const Carusel = ()=>{
  useEffect(()=>{
        const miStorage = window.localStorage;
        let isActive = false;
        const $heart = document.querySelectorAll(".heart");
        //miStorage.clear()
    try{

        for(let i=0;i<miStorage.length;i++){
            const pos = miStorage.getItem(miStorage.key(i));
            if(pos!=null)$heart[pos].style.setProperty("fill","#FF0000");
        }
    }catch(e){
        console.error(e);
    }
        const handlecolor =(e)=>{
       
        if(e.target.matches(".heart")){
          
            for(let i=0;i<$heart.length;i++){
                if(e.target==$heart[i]){
                    isActive =!isActive;
                    miStorage.setItem(`heart${i}`,i)
                    if(isActive)e.target.style.setProperty("fill","#FF0000");
                    else{
                        miStorage.removeItem(`heart${i}`);
                        e.target.style.setProperty("fill","#000000");
                    }
                 }
             }
         }
         
     }
     document.addEventListener("click",handlecolor)
    
  },[])

    return(
        <Splide options={ {
            rewind : true,
            type:'loop',
            perPage:window.screen.width<=500?1:5,
            perMove:1,
            width  : 1400,
            gap    : '1rem',
            autoplay:true,
            hasAutoplayProgress:true,
            hasAutoplayControls:true,
            interval:2000
          } }				
          hasSliderWrapper
          hasAutoplayProgress>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
            <SplideSlide>
                <CardsSection/>
            </SplideSlide>
        </Splide>
    );
}
export default Carusel;