import React,{useEffect,useState} from 'react';
import borde from '../../medias/images/borde.gif';

const Grid4Avanced =(props)=>{
    const[prop,setProp] = useState({backgrounds : [],images : [],title : [], columnVisible : []});
    const img = "https://bulma.io/images/placeholders/1280x960.png";
    const colors = ["red","red","red","red"];
    useEffect(()=>{
        try{
            if(props.images!=null)if(props.images.length>5)throw new Error("Array Index of Exception");
            if(props.backgrounds!=null)if(props.backgrounds.length>5)throw new Error("Array Index of Exception");
            if(props.title!=null)if(props.title.length>4)throw new Error("Array Index of Exception");
            if(props.columnVisible!=null)if(props.columnVisible.length>4)throw new Error("Array Index of Exception");
            setProp({
                backgrounds : props.backgrounds||["red","red","red","red","red"],
                images : props.images||["","","","",""],
                title : props.title||["title","title","title","title"],
                columnVisible : props.columnVisible||["visible","visible","visible","visible"]
            })
        }catch(e){
            console.error(e)
        }

    },[])
    return(
        <div className="columns is-multiline is-5"  style={{background:prop.backgrounds[0],height:"auto",marginLeft:"-1.5rem",marginRight:"-1.5rem",position:"relative"}}>
            <div className="column is-full">
            <img style={{position:"absolute",left:"0",zIndex:"-1",top:"-40px"}} src={borde} alt="borde"/>
            </div>
            <img className="ico" src={prop.images[0]} alt="imagen" style={{position:"absolute",width:"80%",height:"100%",borderRadius:"70%",left:"0",right:"0",margin:"auto"}}/>
            <div className="column is-half breack" style={{visibility:prop.columnVisible[0],paddingTop:"3rem",zIndex:"2"}}>
                <p style={{background:prop.backgrounds[1],height:"calc(300px)"}} class="middle">
                    <img src={prop.images[1]} alt={prop.title[0]}/>
                    <p>{prop.title[0]}</p>
                </p>
            </div>
            <div className="column is-half breack" style={{visibility:prop.columnVisible[1],paddingTop:"3rem",zIndex:"2"}}>
                <p style={{background:prop.backgrounds[1],height:"calc(300px)"}} class="middle">
                    <img src={prop.images[2]} alt={prop.title[0]}/>
                    <p>{prop.title[0]}</p>
                </p>
            </div>
            <div className="column is-half" style={{visibility:prop.columnVisible[2],paddingBottom:"3rem",zIndex:"2"}}>
                <p style={{background:prop.backgrounds[1],height:"calc(300px)"}} class="middle">
                    <img src={prop.images[3]} alt={prop.title[0]}/>
                    <p>{prop.title[0]}</p>
                </p>
            </div>
            <div className="column is-half breack" style={{visibility:prop.columnVisible[3],paddingBottom:"3rem",zIndex:"2"}}>
                <p style={{background:prop.backgrounds[1],height:"calc(300px)"}} class="middle">
                    <img src={prop.images[4]} alt={prop.title[0]}/>
                    <p>{prop.title[0]}</p>
                </p>
            </div>
        </div>
    );
}
export default Grid4Avanced;