import '../../css/inicio.css';
import Grid5 from './grid5';
import '../../hooks/useMsAnimator';
import tec from '../../medias/images/tecnology.jpg';
import {useFade,toolspos,config} from '../../hooks/useMsAnimator';
import react from '../../medias/images/react.png';
import java from '../../medias/images/java.png';
import javascript from '../../medias/images/javascript.png';
import spring from '../../medias/images/spring.png';
import Grid3 from './Grid3';
import ss from '../../medias/images/ssssss.jpg';
import Grid2 from './Grid2';
import Grid16 from './Grid16';
import ff from '../../medias/images/ffff.jpg';
import Grid4Avanced from './grid4Avanced';
import fd from '../../medias/images/fd.jpg';
import s6 from '../../medias/images/ilustracion6s.png';
import s11 from '../../medias/images/ilustracion11s.png';
import s5 from '../../medias/images/ilustracion5s.png';
import s2 from '../../medias/images/ilustracion2s.png';
import s12 from '../../medias/images/ilustracion12s.png';
import borde from '../../medias/images/borde.gif';
import lib from '../../medias/images/libro.png';
import Footer from '../Footer/Footer';
import '../../css/inicio.css';
import Carusel from './Carusel';
const Section = ()=>{
    toolspos.screenanimate=6;
    toolspos.part=4;
    config.time="1s"
   // useFade(".columns");
    return(
        <>
            <section class="section">
                <Grid5 
                    backgrounds={["#29b7dd","#8b72ee","#bd72ef","#bd72ef"]}
                    images={[tec,java,react,javascript,spring]}
                    title={["Java","React","Javascript","Spring"]}
                >
                    <h1 className="is-size-2" style={{position:"relative"}}>Java Fullstack web developer{"\u00a0"}<img style={{position:"absolute",bottom:"0"}} src="https://img.icons8.com/wired/40/4a90e2/web.png"/></h1>
                    <p>Domina estas cuatros tecnologias de desarrollo como un experto</p>
                </Grid5>
                <Grid3 
                    backgrounds={["#3b065e"]}
                    images={[ss,lib,lib]}
                    title={["Logra tus objetivos","“Si no eres obstinado, te rendirás a los experimentos demasiado pronto. Y si no eres flexible, te golpearás la cabeza contra la pared y no verás una solución diferente a un problema que estás tratando de resolver.”Jeff Bezos","“La pregunta que me hago casi todos los días es: ‘¿Estoy haciendo lo más importante que puedo?”Mark Zuckerberg"]}
                >

                </Grid3>
                <Grid2 
                    backgrounds={["none"]}
                    images={[s12]}
                    component={<>
                        <br/>
                        <br/>
                        <div className="">
                        <h1 className="title is-2" style={{color:"#00d1b2",fontWeight:"bold"}}>Estudia con nosotros cuando quieras y donde quieras.</h1>
                        <p>¿Estas cansado del sistema educativo? de profesores que solo llegan a llenar sus horas de 
                            trabajo pero cuando vas a preguntarles no existen... pues felicidadesdejame contarte, asi fue
                            como surgió este proyecto  
                        </p>
                        <p><span style={{color:"#3b065e",fontWeight:"bold"}}>Ms academy</span> es un lugar donde puedes aprender de forma gratuita sin menbresias caras 
                            sin necesidad de estar tras de alguien que no te hace caso ademas nos encontramos 
                            con la capacidad de compartir mi conocimiento y hacer mas grande la comunidad Java
                        </p>
                        </div>
                    </>}>
                    
                </Grid2>
                <Grid16 
                    images={[s6,s11,s5,s2]}
                    columnVisible={["visible","none","hidden","hidden"]}
                    backgrounds={["#3b065e","#00d1b2","#ffffff","#ffffff","#00d1b2"]}
                    title={["Aprender a conocer","Aprender a hacer","Aprender a ser","Aprender a vivir juntos"]}>
                    <img style={{position:"absolute",left:"0",zIndex:"-1",top:"-40px"}} src={borde} alt="borde"/>
                    <p className="title tag is-primary is-large">Los cuatro pilares de la educación{"\u00a0"}<img src="https://img.icons8.com/dusk/25/4a90e2/flip-chart.png" alt="icono"/></p>
                </Grid16>
                <Grid3 
                    backgrounds={["none"]}
                    images={[ff]}
                    title={["Posicionate en el mundo laboral"
                            ,<>
                            <h1 className="title" style={{color:"#00d1b2",fontWeight:"bold"}}>
                                Al día de hoy
                            </h1>
                            <p style={{color:"black"}}>Al día de hoy los trabajos como programadores
                                abundan pero pocos llegan a ser buenos he incluso hay muchos que saliendo de la
                                universidad no saben programar esto realmente es asi y no es tan dificil de creer.
                            </p>
                              </>
                            ,<>
                            <br/>
                            <h1 className="title" style={{color:"#8b72ee",fontWeight:"bold"}}>
                                En mi experiencia
                            </h1>
                            <p style={{color:"black"}}>
                                Lo que yo te puedo contar es que todo lo que aprendí vino de libros, cursos en linea
                                leyendo las documentaciones, que si bien estas últimas aveces son muy confusas,
                                uno de mis profesores en quien me inspire es Juan Díaz de{"\u00a0"} 
                                <a href="https://www.pildorasinformaticas.es/" target="_blank" rel="noreferrer">pildorasinformaticas.com</a>
                            </p>
                            <br/>
                            </>]}
                >
                </Grid3>
                <Grid4Avanced
                    backgrounds={["#3b065e","rgb(255,255,255,0)","","","rgb(255,255,255,0)"]}
                    columnVisible={["hidden","hidden","visible","hidden"]}
                    images={[fd,"",""]}
                    title={[<div style={{padding:"20px", width:"100%", height:"100%"}}>
                            <article class="message is-link">
                                <div class="message-header" style={{textAlign:"center"}}>
                                    <p className="is-2" style={{color:"#00d1b2",fontWeight:"bold"}}>
                                        "Consigue tu certificado"
                                    </p>
                                </div>
                                <div class="message-body">
                                    <p> Recuerda:</p>
                                    <p>
                                        cada 6 meses tendras derecho a un exámen el cual sera
                                        totalmente gratuito pero como mencione anteriormente solo tienes 
                                        una oporunidad cada 6 meses, asi que procura estar bien preparado...
                                    </p>
                                </div>
                            </article>
                            </div>,"","",""]}
                    >
                   
                </Grid4Avanced>
                <Carusel/>
                <Footer margin="-1.5rem">
                    
                </Footer>
            </section>

            
        </>
    );
}
export default Section;